.section {
    flex: 0 0 600px;
}

.scroll {
    overflow-y: auto;
    max-height: 464px;
    scrollbar-width: thin;
    scrollbar-color: #8585AD #2F2F37;
    padding: 0;
    list-style: none;
}

.scroll::-webkit-scrollbar {
    background-color: #2F2F37;
    width: 8px;
}

.scroll::-webkit-scrollbar-thumb {
    background-color: #8585AD;
}

.scroll > :first-child {
    margin-top: 0;
}

.empty-element {
    display: flex;
    align-items: center;
    text-align: center;
    background: var(--background);
}

.empty-element-text {
    flex: 1;
}

.ingredient {
    background: var(--background);
}
